<template>
  <div class="cms-list-layout">
    <cms-prompt collapse="true">
      <li>显示买家售后申请记录</li>
      <li>买家可对订单内的单件商品进行申请售后操作</li>
    </cms-prompt>
    <!-- 筛选区 -->
    <section class="cms-list-query">
      <div class="query-group">
        <label>服务单号</label>
        <el-input @keyup.native.enter="query" v-model="queryParams.refundNum"></el-input>
      </div>
      <div class="query-group">
        <label>订单号</label>
        <el-input @keyup.native.enter="query" v-model="queryParams.orderNum"></el-input>
      </div>
      <div class="query-group">
        <label>申请时间</label>
        <cms-picker v-model="timer" @change="setTimer"></cms-picker>
      </div>
      <div class="query-group">
        <label>服务类型</label>
        <el-select @change="query" v-model="queryParams.refundType">
          <el-option label="全部" value=" "></el-option>
          <el-option
            v-for="item in orderStatus"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </div>
      <div class="query-group">
        <label>服务状态</label>
        <el-select v-model="queryParams.status" @change="query">
          <el-option label="全部" :value="''"></el-option>
          <el-option label="待卖家确认" :value="0"></el-option>
          <el-option label="退款中" :value="1"></el-option>
          <el-option label="待买家发货" :value="4"></el-option>
          <el-option label="待卖家收货" :value="5"></el-option>
          <el-option label="待买家收货" :value="6"></el-option>
          <el-option label="待卖家发货" :value="7"></el-option>
          <el-option label="已完成" :value="8"></el-option>
          <el-option label="买家已取消" :value="9"></el-option>
        </el-select>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker="true" @click="query">查询</cms-button>
      </div>
    </section>
    <el-tabs v-model="queryParams.refundType" @tab-click="changeOrderStatus">
      <el-tab-pane label="全部" name=" "></el-tab-pane>
      <el-tab-pane
        v-for="item in orderStatus"
        :key="item.value"
        :label="item.label"
        :name="item.value"
      ></el-tab-pane>
    </el-tabs>
    <!-- 数据列表 -->
    <table class="order-table" v-loading="loading">
      <tr>
        <th>服务单信息</th>
        <th>数量</th>
        <th>退款明细</th>
        <th>服务单类型</th>
        <th>服务单状态</th>
        <th>申请时间</th>
        <th>操作</th>
      </tr>
      <tbody v-for="(item,index) in tableData" :key="index">
        <div style="width:100%;height:25px;background:#fff;"></div>
        <!-- 头部 -->
        <tr class="order-header">
          <td colspan="11" style="border:1px solid #e0e0e0;">
            <span style="margin-left:20px">服务单号</span>
            <span style="margin-right: 50px;">{{item.orderRefund.orderRefundNum}}</span>
            <span style="margin-left:20px">订单编号：</span>
            <span style="margin-right: 50px;">{{item.order.orderCode}}</span>
          </td>
        </tr>
        <tr>
          <td class="product-info">
            <!-- 商品信息 -->
            <div class="product__inner">
              <img
                class="product-img"
                :src="$cms.filterUrl(item.productVO.product.coverImage)"
                v-if="item.productVO.product"
              >
              <div class="product-title">
                <h3 class="title-h3">{{item.productVO.product.name}}</h3>
                <p
                  class="title-p"
                  style="margin-top:10px;"
                >{{showAttr(item.productVO.proSkuAttrList)}}</p>
              </div>
            </div>
          </td>
          <td class="order-count">x{{item.orderRefund.productRefundCount}}</td>
          <td class="order-count" style="min-width:130px;text-align:left;padding-left:37px;">
            <div>
              <span>金额:</span>
              <span>{{$cms.float(item.orderRefund.refundAmount)}}</span>
            </div>
            <div>
              <span>红包:</span>
              <span>{{item.orderRefund.refundRedPacket}}</span>
            </div>
            <div>
              <span>积分:</span>
              <span>{{item.orderRefund.refundIntegral}}</span>
            </div>
          </td>
          <td class="order-count text-center" style="min-width:130px">
            <span v-if="item.orderRefund.refundType==1">仅退款</span>
            <span v-if="item.orderRefund.refundType==2">退款/退货</span>
            <span v-if="item.orderRefund.refundType==3">换货</span>
          </td>
          <td class="order-count text-center cell" style="min-width:130px"
          >
          <span :class="statusClass(item.orderRefund.status)">
             {{item.orderRefund.status | statusFilter}}
          </span>
         </td>
          <td
            class="order-count text-center"
            style="min-width:140px"
          >{{$moment(item.orderRefund.applyTime)}}</td>
          <td class="order-operation">
            <div class="mb-10">
              <el-button type="text"
                @click="$routerLink('/after/service/fundAndChange','view',
               {id:item.orderRefund.id,refundType:item.orderRefund.refundType})"
              >详情</el-button> 
            </div>
            <div class="mb-10" v-if="item.orderRefund.status==0">
              <cms-button
                size="small"
                type="info"
                style="width:100px"
                @click="checkService(item,true)"
              >确认</cms-button>
            </div>
            <div class="mb-10" v-if="item.orderRefund.status==0">
              <cms-button
                size="small"
                type="info"
                style="width:100px"
                @click="checkService(item,false)"
              >确认不通过</cms-button>
            </div>
            <div class="mb-10" v-if="item.orderRefund.status==5">
              <cms-button
                size="small"
                type="info"
                style="width:100px"
                @click="checkService(item,true)"
              >确认收货</cms-button>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
    <cms-pagination @change="getPage" :total="total" :size="5"></cms-pagination>
    <!-- 确认操作 -->
    <check-service ref="checkService" @change="changeService"></check-service>
  </div>
</template>
<script>
import list from '@/mixins/list'
import { refundPage } from './../../../api/shop/order.js'
import checkService from './checkService'
var defaultData = {
  page: 1,
  size: 5,
  status: '',
  payMode: '',
  refundNum: '',
  orderNum: '',
  applyTimeBegin: '',
  applyTimeEnd: '',
  refundType: ' ',
  isPlatformIntervent: ''
}
export default {
  mixins: [list],
  components: {
    checkService
  },
  data() {
    return {
      timer: [],
      activeStatus: '',
      orderStatus: [
        {
          label: '退款单',
          value: '1'
        },
        {
          label: '退货单',
          value: '2'
        },
        {
          label: '换货单',
          value: '3'
        }
      ],
      queryParams: Object.assign({}, defaultData)
    }
  },
  filters: {
    statusFilter(status) {
      var statues = [
        '待卖家确认',
        '退款中',
        '卖家拒绝',
        '',
        '待买家发货',
        '待卖家收货',
        '待买家收货',
        '待卖家发货',
        '已完成',
        '买家取消'
      ] // 状态值对应的属性
      return statues[parseInt(status)]
    }
  },
  methods: {
    changeService(status) {
      // 操作成功的回调
      if (status) {
        this.query()
      }
    },
    checkService(item, status) {
      // 执行售后操作
      this.$refs.checkService.checkService(item, status)
    },
    changeOrderStatus(value) {
      this.query()
    },
    setTimer(timer) {
      // 设置时间区间
      this.queryParams.applyTimeBegin = timer[0]
      this.queryParams.applyTimeEnd = timer[1]
      this.query()
    },
    showAttr(data) {
      // 转换json字符串属性
      if (data.length <= 0) {
        return ''
      } else {
        let str = ''
        data.forEach(v => {
          const tmp = v.attrName + ':' + v.attrValueName + '  '
          str = str + tmp
        })
        return str
      }
    },
    statusClass(status) {
      var statues = [
        'info',
        'info',
        'error',
        '',
        'info',
        'info',
        'info',
        'info',
        'success',
        'info'
      ] // 状态值对应的属性
      return statues[parseInt(status)]
    }
  },
  created() {
    this.getTableData(refundPage, this.queryParams)
  }
}
</script>

<style scoped lang='scss'>
</style>




